
.ratingSelect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  font-size: 0;
  position: relative;
}
.ratingSelect .rating-type::after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    border: 1px solid rgba(7, 17, 27, 0.1);
    content: '';
}
.ratingSelect .rating-type .block {
    display: inline-block;
    padding: 8px 12px;
    margin-right: 8px;
    line-height: 16px;
    border-radius: 2px;
    font-size: 12px;
    color: #4d555d;
}
.ratingSelect .rating-type .block .count {
      font-size: 8px;
      margin-left: 2px;
}
.ratingSelect .rating-type .block.active {
      color: #fff;
}
.ratingSelect .rating-type .block.positive {
      background: rgba(0, 160, 220, 0.2);
}
.ratingSelect .rating-type .block.positive.active {
        background: #00a0dc;
}
.ratingSelect .rating-type .block.negative {
      background: rgba(77, 85, 93, 0.2);
}
.ratingSelect .rating-type .block.negative.active {
        background: #4d555d;
}
.ratingSelect .switch {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.2);
  color: #93999f;
}
.ratingSelect .switch .icon-checkcircle {
    font-size: 24px;
    margin-right: 4px;
}
.ratingSelect .switch .icon-checkcircle.active {
      color: #00c850;
}
.ratingSelect .switch .text {
    font-size: 12px;
}



