
.cartcontrol {
  font-size: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cartcontrol .move-enter-active, .cartcontrol .move-leave-active {
    -webkit-transition: all .5s;
    transition: all .5s;
}
.cartcontrol .move-enter, .cartcontrol .move-leave-to {
    opacity: 0;
    -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
            transform: translate3D(24px, 0, 0) rotate(180deg);
}
.cartcontrol .cart-decrease, .cartcontrol .cart-increase {
    display: inline-block;
    padding: 0 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}
.cartcontrol .cart-count {
    display: inline-block;
    width: 12px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: #93999f;
}



