
@charset "UTF-8";
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  z-index: 50;
  background: #000;
}
.shopcart .content-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    /*height: 48px;*/
}
.shopcart .content-wrapper .content-left {
      background: #141d27;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.shopcart .content-wrapper .content-left .logo-wrapper {
        position: relative;
        bottom: 12px;
        display: inline-block;
        width: 58px;
        height: 58px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        padding: 6px;
        margin: 0 12px;
        border-radius: 50%;
        background: #141d27;
}
.shopcart .content-wrapper .content-left .logo-wrapper .logo {
          background: #2b343c;
          height: 100%;
          width: 100%;
          border-radius: 50%;
          text-align: center;
}
.shopcart .content-wrapper .content-left .logo-wrapper .logo.hasfood {
            background: #00a0dc;
}
.shopcart .content-wrapper .content-left .logo-wrapper .logo .icon-shoppingcartblackshape {
            line-height: 44px;
            font-size: 24px;
            color: #80858a;
}
.shopcart .content-wrapper .content-left .logo-wrapper .logo .icon-shoppingcartblackshape.hasfood {
              color: white;
}
.shopcart .content-wrapper .content-left .logo-wrapper .count {
          position: absolute;
          right: 0;
          top: 0;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          border-radius: 16px;
          font-size: 9px;
          font-weight: 700;
          color: white;
          background: #f01414;
          -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
                  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcart .content-wrapper .content-left .price {
        display: inline-block;
        vertical-align: top;
        color: #80858a;
        padding-right: 12px;
        margin: 12px 0;
        font-size: 16px;
        font-weight: 700;
        line-height: 24px;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.shopcart .content-wrapper .content-left .price.hasfood {
          color: #fff;
}
.shopcart .content-wrapper .content-left .desc {
        display: inline-block;
        line-height: 24px;
        vertical-align: top;
        font-size: 10px;
        color: rgba(255, 255, 255, 0.4);
        margin: 12px 0 0 12px;
}
.shopcart .content-wrapper .content-right {
      width: 105px;
      -webkit-box-flex: 0;
          -ms-flex: 0 0 105px;
              flex: 0 0 105px;
}
.shopcart .content-wrapper .content-right .pay {
        height: 48px;
        line-height: 48px;
        text-align: center;
        color: rgba(255, 255, 255, 0.4);
        font-weight: 700;
        background: #2b343c;
        font-size: 12px;
}
.shopcart .content-wrapper .content-right .pay.highlight {
          color: #fff;
          background: green;
}
.shopcart .ball-container {
    /*未完*/
}
.shopcart .ball-container .ball {
      display: none;
      position: fixed;
      left: 32px;
      bottom: 22px;
      z-index: 200;
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
}
.shopcart .ball-container .ball .inner {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #00a0dc;
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
}
.shopcart .shopcart-list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
.shopcart .shopcart-list.fold-enter-active, .shopcart .shopcart-list.fold-leave-active {
      -webkit-transition: all .3s;
      transition: all .3s;
}
.shopcart .shopcart-list.fold-enter, .shopcart .shopcart-list.fold-leave-to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
.shopcart .shopcart-list .list-header {
      height: 40px;
      line-height: 40px;
      padding: 0 18px;
      background: #f3f5f7;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shopcart .shopcart-list .list-header .title {
        float: left;
        font-size: 14px;
        color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
        float: right;
        font-size: 12px;
        color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
      max-height: 217px;
      background: #fff;
      overflow: hidden;
      padding: 0 18px;
}
.shopcart .shopcart-list .list-content .food {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        height: 48px;
        line-height: 48px;
        position: relative;
}
.shopcart .shopcart-list .list-content .food::after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border: 1px solid rgba(7, 17, 27, 0.1);
          content: '';
}
.shopcart .shopcart-list .list-content .food .name {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          font-size: 14px;
          font-weight: 700;
          color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
          font-size: 14px;
          font-weight: 700;
          color: #f01414;
          padding: 0 12px 0 18px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  background: rgba(7, 17, 27, 0.6);
}
.list-mask.fade-enter-active, .list-mask.fade-leave-active {
    -webkit-transition: all .2s;
    transition: all .2s;
}
.list-mask.fade-enter, .list-mask.fade-leave-to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}



